.home-container {
  display: grid;
  grid-template-rows: auto 3rem 12.2rem;
  justify-items: stretch;
  -ms-flex-align: stretch;
      align-items: stretch;
  overflow-y: hidden;
}
.home-cards-header {
  padding: 0 1rem;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}
.recent-txt {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
}
.home-mm {
  text-align: center;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
}
.home-mm-content {
  margin: auto 0;
}
input.search-input-home {
  font-size: 1rem;
  display: block;
  background-color: var(--background-offset);
  color: var(--txt);
  width: 30rem;
  text-align: center;
  font-weight: 700;
  padding: 1rem 2rem;
  border-radius: 0.3rem;
  outline: none;
  -webkit-transition: background-color 320ms, -webkit-box-shadow 120ms;
  transition: background-color 320ms, -webkit-box-shadow 120ms;
  -o-transition: background-color 320ms, box-shadow 120ms;
  transition: background-color 320ms, box-shadow 120ms;
  transition: background-color 320ms, box-shadow 120ms, -webkit-box-shadow 120ms;
}
input.search-input-home:focus {
  position: relative;
  z-index: 3;
}
input.search-input-home:focus::-webkit-input-placeholder {
    color:  rgba(0, 0, 0, 0.158);
     /* text-indent: -999px */
}
input.search-input-home:focus:-ms-input-placeholder {
    color:  rgba(0, 0, 0, 0.158);
     /* text-indent: -999px */
}
input.search-input-home:focus::-ms-input-placeholder {
    color:  rgba(0, 0, 0, 0.158);
     /* text-indent: -999px */
}
input.search-input-home:focus::placeholder {
    color:  rgba(0, 0, 0, 0.158);
     /* text-indent: -999px */
}
.menu-search-home {
  background-color: var(--background);
  position: absolute;
  /* min-width: initial; */
  width: 29rem;
  margin: 0 0.5rem;
  border-radius: 0 0 0.5rem 0.5rem;
  /* height: fit-content; */
  -webkit-box-shadow: var(--elevation-1);
          box-shadow: var(--elevation-1);
  z-index: 6;
}
.help-txt {
  color: #bbbbbb;
  padding: 1rem 0;
  font-weight: 700;
}
.menu-search-home .scrollable {
  max-height: 40vh;
  padding: 0.8rem 0;
  overflow-y: scroll;
}
.home-cards-container {
  display: grid;
  grid-template-rows: 1fr;
  grid-auto-flow: column;
  padding-top: 1.5rem;
  padding-left: 0.15rem;
  -ms-flex-pack: start;
      justify-content: start;
  overflow-x: scroll;
}
.home-cards-container .order-card {
  -webkit-transition: -webkit-transform var(--transition), -webkit-box-shadow var(--transition);
  transition: -webkit-transform var(--transition), -webkit-box-shadow var(--transition);
  -o-transition: transform var(--transition), box-shadow var(--transition);
  transition: transform var(--transition), box-shadow var(--transition);
  transition: transform var(--transition), box-shadow var(--transition), -webkit-transform var(--transition), -webkit-box-shadow var(--transition);
}
.home-cards-container .order-card:hover {
  -webkit-transform: translate(0, -0.5rem);
      -ms-transform: translate(0, -0.5rem);
          transform: translate(0, -0.5rem);
}
.home-cards-container .order-card:active {
  -webkit-transform: translate(0, -1rem);
      -ms-transform: translate(0, -1rem);
          transform: translate(0, -1rem);
}
.home-cards-container:hover {
  -webkit-transform: translate(0, 0);
      -ms-transform: translate(0, 0);
          transform: translate(0, 0);
}

.home-cards-container::-webkit-scrollbar {
  display: none;
}
@media (max-width: 768px) {
  .home-cards-header {
    -ms-flex-pack: center;
        justify-content: center;
  }
  .recent-txt {
    display: none;
  }
  .home-container {
    grid-template-rows: auto 3rem 10rem;
    overflow-y: scroll;
  }
  .home-cards-container {
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    -ms-flex-pack: center;
        justify-content: center;
    overflow-y: scroll;
    -webkit-transform: translate(0, 0);
        -ms-transform: translate(0, 0);
            transform: translate(0, 0);
  }
}



