.modal-container {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-line-pack: center;
      align-content: center;
  z-index: 10;
  overflow-y: scroll;
}
/* Custom Scrollbar */
.modal-container::-webkit-scrollbar {
  width: 0px;
}
.modal {
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  min-width: 20rem;
  padding: 0.5rem 0;
  margin: 2rem 0;
  margin-top: auto;
  margin-bottom: auto;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}

/* Header */
.modal .modal-header {
  width: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-align: center;
      align-items: center;
  padding: 0.5rem 2rem;
  margin-bottom: 1.5rem;
  border-radius: 0.35rem 0.35rem 0 0;
}
.modal .modal-header .txt {
  letter-spacing: 0.6px;
}
.modal .modal-header .btn {
  margin: 0;
}
/* Body */
.modal .modal-body {
  padding: 0 2rem;
}
.modal-body .body-header {
  width: calc(100% + 4rem);
  background-color: var(--offset-light);
  margin: 0 -2rem;
  margin-top: -1rem;
  margin-bottom: 1rem;
}
.modal-body .body-header .txt {
  padding: 1rem 2rem;
}
.modal-body .body-header .btn {
  margin: 0 2rem;
}
.modal-body .body-content {
  padding: 0.5rem;
}
.modal-body .body-content-footer {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  margin: 0 -1rem;
  margin-top: 1.5rem;
}
.modal-body .body-content-footer .btn {
  width: calc(100% - 2rem);
}

/* Footer */
.modal .modal-footer {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: end;
      justify-content: flex-end;
  -ms-flex-align: center;
      align-items: center;
  margin-top: 1.5rem;
  padding: 0.5rem 1.5rem;
  /* margin-top: 2rem; */
}
.modal .modal-footer .btn {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-align: center;
      align-items: center;
  padding-left: 0.8rem;
  padding-right: 0.8rem;
}
.modal .modal-footer .btn .fas {
  margin-left: 0.5rem;
}



