table.datatable {
  font-size: 0.85rem;
  width: auto;
  padding: 0;
  margin-bottom: 1.5rem;
}
.inline-datatable-conatiner {
  display: grid;
  /* grid-template-columns: 1fr 1fr auto; */
  grid-auto-flow: column;
  grid-gap: 1.5rem;
}
.datatable th,
.datatable td {
  border: 1px solid #949494;
}
.datatable th {
  font-size: 1rem;
  letter-spacing: 1px;
  background-color: #949494;
  color: #ffffff;
  padding: 0.6rem 1rem;
  white-space: nowrap;
}
.datatable td {
  font-weight: 600;
  width: 5rem;
  height: 1.8rem;
  padding: 0;
  margin: 0;
  white-space: nowrap;
  -webkit-box-sizing: content-box;
          box-sizing: content-box;
}

.filled-txt {
  background-color: var(--background-offset);
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  height: inherit;
  /* width: auto; */
  padding: 0.5rem;
}
.static-datatable .filled-txt {
  min-width: 1rem;
  min-height: 2rem;
}
.auto-filled-txt {
  background-color: #e0f0ad;
}
.span-prefix {
  color: var(--txt-offset);
  font-size: 0.9rem;
  padding-right: 0.2rem;
  font-weight: 700;
  z-index: 3;
}
.span-suffix {
  color: var(--txt-offset);
  font-size: 0.9rem;
  padding: 0 0.3rem;
  padding-left: 0.2rem;
  font-weight: 700;
  z-index: 3;
}
.span-input-prefix {
  color: var(--txt-offset);
  font-size: 0.9rem;
  padding-left: 0.3rem;
  margin-right: -0.8rem;
  font-weight: 700;
  z-index: 3;
}
.span-input-prefix + .input-prefix {
  padding-left: 1.1rem !important;
}
.span-input-prefix-long {
  color: var(--txt-offset);
  font-size: 0.9rem;
  padding-left: 0.3rem;
  margin-right: -2rem;
  font-weight: 700;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  z-index: 3;
}
.span-input-prefix-long + .input-prefix {
  padding-left: 2.3rem !important;
}
.span-input-suffix {
  color: var(--txt-offset);
  font-size: 0.9rem;
  padding: 0 0.3rem;
  padding-left: 0.2rem;
  margin-left: -1.2rem;
  font-weight: 700;
  z-index: 3;
}

/* Input */
table.datatable td input {
  background-color: #ffffff;
  width: 100%;
  height: 100%;
  padding: 0.3rem;
  border-width: 0;
  /* text-align: right; */
  cursor: cell;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  outline: none;
  -webkit-transition: -webkit-box-shadow 100ms;
  transition: -webkit-box-shadow 100ms;
  -o-transition: box-shadow 100ms;
  transition: box-shadow 100ms;
  transition: box-shadow 100ms, -webkit-box-shadow 100ms;
}
table.datatable td input:hover {
  position: relative;
  border-radius: 0.1rem;
  -webkit-box-shadow: 0 0 0 0.2rem #797979;
          box-shadow: 0 0 0 0.2rem #797979;
  z-index: 2;
}
table.datatable td input:focus {
  text-align: left;
  -webkit-box-shadow: inset 0 0 0 0.2rem #d1d1d1;
          box-shadow: inset 0 0 0 0.2rem #d1d1d1;
  z-index: 1;
}

/* Prefix and Suffix */
.input-container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: nowrap;
      flex-wrap: nowrap;
  -ms-flex-pack: start;
      justify-content: flex-start;
  -ms-flex-align: center;
      align-items: center;
  width: 100%;
  height: 100%;
}
.input-prefix {
  padding-left: 1.2rem !important;
  width: calc(100% - 1rem);
  min-width: 2rem;
}
.input-suffix {
  padding-right: 1.2rem !important;
  width: calc(100% - 1rem);
  min-width: 2rem;
}

/* Selet input */

.datatable td select {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  border-width: 0;
  cursor: pointer;
  outline: none;
}
.datatable td select:hover {
  position: relative;
  border-radius: 0;
  -webkit-box-shadow: 0 0 0 0.2rem #636363a1;
          box-shadow: 0 0 0 0.2rem #636363a1;
  z-index: 1;
}
.datatable td select:focus {
  position: relative;
  border-radius: 0;
  background: #ffffff;
  -webkit-box-shadow: 0 0 0 2px rgb(255, 255, 255), 0 0 0 5px rgb(65, 190, 92);
          box-shadow: 0 0 0 2px rgb(255, 255, 255), 0 0 0 5px rgb(65, 190, 92);
  z-index: 2;
}

/* Remove number input arrow */
.datatable td input[type="number"] {
  -moz-appearance: textfield;
}
.datatable td input[type="number"]::-webkit-inner-spin-button,
.datatable td input[type="number"]::-webkit-outer-spin-button {
  -webkit-appearance: none;
  -moz-appearance: textfield;
  margin: 0;
}
.datatable td input[type="date"] {
  width: 100%;
  font-size: 2vw;
  padding: 0px;
  padding-left: 2px !important;
  padding-right: 2px !important;
  margin: 0;
  border-width: 0;
  -moz-appearance: textfield;
}
.datatable td input[type="date"]::-webkit-inner-spin-button,
.datatable td input[type="date"]::-webkit-outer-spin-button {
  -webkit-appearance: none;
  -moz-appearance: textfield;
  margin: 0;
}



